@use '../common//commonVariables.scss' as cv;
@use './variables.scss' as v;

// 계약정보
.contractInfoContainer {
    .contractInfoBox {
        @include v.grey-round-box();
        width: auto; height: auto;
        padding: cv.vw-font(10px, 28px) cv.vw-font(13px, 25px);
    }
}
.contractTitle {
    margin-bottom: 11px;
    font-size: cv.vw-font(10.5px, 19.5px);
    font-weight: 500;
}
.contractContent {
    color: #F21717;
    margin-bottom: 11px;
}
.contractContentDetail {
    color: #8F9191;
    text-align: justify;
    line-height: cv.vw-font(14px, 19px);
}